import { Component, ViewEncapsulation, Renderer2 } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { Validators } from '@angular/forms';

@Component({
  selector: 'app-header-menu',
  templateUrl: './header-menu.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./header-menu.component.css']
})

export class HeaderMenuComponent {
  closeResult: string;

  public router = '';
  public login = false;
  public signIn = true;

  email = new FormControl( '', [
    Validators.required,
    Validators.email,
  ]);

  lpassword = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);

  constructor(
    private modalService: NgbModal,
    private _router: Router,
    private renderer: Renderer2
    ) {
    this.router = _router.url;

    console.log(this.router);

    if (this.router !== '/home') {
      this.renderer.addClass(document.body, 'active-header');
    }
  }

  openBackDropCustomClass(content) {
    this.modalService.open(content, { backdropClass: 'light-blue-backdrop' });
  }

  openWindowCustomClass(content) {
    this.modalService.open(content, { windowClass: 'dark-modal' });
  }

  openSm(content) {
    this.modalService.open(content, { size: 'sm' });
  }

  openLg(content1) {
    this.modalService.open(content1, { centered: true, size: 'sm' });
  }

  openVerticallyCentered(content) {
    this.modalService.open(content, { centered: true, size: 'sm' });
  }

  onSubmit( event ) {
    event.stopPropagation();
    event.preventDefault();

    if ( (this.email.value) !== ('test@gmail.com') ) {
     // this.renderer.addClass(document.getElementById("demo"), 'active-header');
     
      this.login  = true;
      this.signIn = false;
      console.log(this.email.value);
      console.log(this.login);
    }
  }
}
