import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CitiesComponent } from './cities/cities.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { FaqComponent } from './faq/faq.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { BookingsComponent } from './bookings/bookings.component';
import { SearchResultComponent } from './search-result/search-result.component';
import { MycreditsComponent } from './mycredits/mycredits.component';
import { PromotionsComponent } from './promotions/promotions.component';
import { InviteFriendsComponent } from './invite-friends/invite-friends.component';
import { HowItWorksComponent } from './how-it-works/how-it-works.component';
import { CallSupportComponent } from './call-support/call-support.component';
import { HotelDetailsComponent } from './hotel-details/hotel-details.component';
import { BookingDetailsComponent } from './booking-details/booking-details.component';
import { HotelCaurosalsComponent } from './hotel-caurosals/hotel-caurosals.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'cities', component: CitiesComponent},
  { path: 'faq', component: FaqComponent },
  { path: 'contact', component: ContactUsComponent },
  { path: 'bookings', component: BookingsComponent },
  { path: 'search-result', component: SearchResultComponent },
  { path: 'mycredits', component: MycreditsComponent },
  { path: 'promotions', component: PromotionsComponent },
  { path: 'invite-friends', component: InviteFriendsComponent },
  { path: 'how-it-works', component: HowItWorksComponent },
  { path: 'call-support', component: CallSupportComponent },
  { path: 'hotel-details', component: HotelDetailsComponent },
  { path: 'booking-details', component: BookingDetailsComponent },
  { path: 'hotel-caurosals', component: HotelCaurosalsComponent },
  { path: '', redirectTo: '/home', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
